import React from "react";
import ReactDOM from "react-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import "./index.css";

import { arachne } from "./arachne.js";
import { Help } from "./settings.js";
import { Zettel } from "./zettel.js";

const colors = {
    shadow: "#3c6e71"
}

class MainMenu extends React.Component{
    constructor(props){
        super(props);
        const menuLst = {
            "Zettel- und Wortmaterial": {
                visible: false, 
                items: {"Zettel-Datenbank": "zettel"/*,
                "Lemmaliste": "lemma",
            "Volltext-Suche": "fulltext"*/}
            },
            /*"Verzeichnisse": {
                visible: false, 
                items: {
                "opera-maiora":"maiora",
                "opera-minora":"minora",
                "Ressourcen":"ressources",
                "Sekundärliteratur":"seklit"
            }},*/
            /*"Editor": {
                visible: false, 
                items:
                {"Projektübersicht":"overview"}
            },*/
            "Einstellungen": {
                visible: false, 
                items: {
                "Hilfe":"help",
                /*"Konto":"account",
                "Statistiken":"stats",*/
                "abmelden":"logout"
            }}
        };
        this.state = {menuEntries: menuLst, currentSubmenu: null};
    }
    render(){
        let style = {
            position: "fixed",
            top: 0,
            left: 0,
            boxShadow: `0 0 2px ${colors.shadow}`,
            padding: "10px 15px",
            zIndex: 1000000,
            backgroundColor: "white"
        };
        let menuEntries = [];
        if(this.props.onTop===1){
            // top menu
            style.right = 0;
            style.display = "flex";
            style.position = "absolute";
            style.justifyContent = "space-evenly";
            let keyCount = -1;
            for(const mainEntry in this.state.menuEntries){
                keyCount ++;
                let subMenu = "";
                const subMenuStyle = {
                    position: "absolute",
                    boxShadow: "rgb(60, 110, 113) 0px 0px 2px",
                    backgroundColor: "white",
                    top: "43px",
                    padding: "10px 25px",
                    marginLeft: "-10px"
                }
                if(this.state.currentSubmenu===mainEntry){
                    let subMenuEntries = [];
                    for(const subEntry in this.state.menuEntries[mainEntry].items){
                        subMenuEntries.push(<div key={subEntry} style={{margin: "10px 0 10px 10px"}} onClick={()=>{
                            this.setState({currentSubmenu: ""});
                            this.props.loadMain(this.state.menuEntries[mainEntry].items[subEntry]);
                        }}>{subEntry}</div>);
                    }
                    subMenu = <div key={subMenuEntries} style={subMenuStyle}>{subMenuEntries}</div>;
                }
                menuEntries.push(
                <div key={keyCount}>
                    <span onClick={() => {this.setState({currentSubmenu: mainEntry})}}>{mainEntry}</span>
                    {subMenu}
                </div>);
                //<div style={{display: "none"}}>{this.state.menuEntries[mainEntry]}</div>
            }
        } else {
            // side menu
            //style.display = "block";
            style.transition = "left 0.5s";
            if(this.state.hidden){style.left = "-290px"}
            style.bottom = "0";
            style.width = "300px";
            let keyCount = -1;
            for(const mainEntry in this.state.menuEntries){
                menuEntries.push(<div key={mainEntry} style={{marginTop: "15px"}}>{mainEntry}</div>);
                for(const subEntry in this.state.menuEntries[mainEntry].items){
                    keyCount ++;
                    menuEntries.push(<div key={subEntry} style={{marginLeft: "15px", display: "inline-block"}} onClick={()=>{
                        this.props.loadMain(this.state.menuEntries[mainEntry].items[subEntry]);
                    }}>{subEntry}</div>);
                    menuEntries.push(<br key={keyCount} />);
                }
            }

        }
        return (
        <nav className="mainMenu" style={style} onClick={() => {
            if(this.props.onTop!=1){
                if(this.state.hidden){this.setState({hidden: false})}
                else{this.setState({hidden: true})}
            }
        }}>
            {menuEntries}            
        </nav>);
    }
}
class App extends React.Component {
    constructor(props){
        const maxWinWidth = 1000;
        super(props);
        // check here, if Login-Screen needs to be shown, or not!
        let mainMenuPos = 1;
        if(window.innerWidth<maxWinWidth){mainMenuPos = 0}
        this.state = {login: false, res: null, mainMenuPos: mainMenuPos};
        window.addEventListener("resize", () => {
            if(window.innerWidth<maxWinWidth){this.setState({mainMenuPos: 0})}
            else{this.setState({mainMenuPos: 1})}
        });
    }
    loadMain(res){
        if(res==="logout"){
            arachne.close();
            this.setState({
                login: false,
                res: null
            });
        } else {
            this.setState({res: res});
        };
    }
    render(){
        if(this.state.login){
            // hurra, login succeeded!
            let main = null;
            switch(this.state.res){
                case null:
                    break;
                case "help":
                    main = <Help />;
                    break;
                case "zettel":
                    main = <Zettel />;
                    break;
                default:
                    throw `UNKNOWN RES IN 'loadMain': ${this.state.res}`
            }
            return (
                <div style={(this.state.mainMenuPos===1)?{marginTop: "60px"}:{marginLeft: "40px"}}>
                    <MainMenu onTop={this.state.mainMenuPos} loadMain={res => {this.loadMain(res)}} />
                    {main}
                </div>
            );
        } else {
            return (
            <div style={{
                position: "absolute",
                display: "grid",
                height: "100%",
                width: "100%",
                gridTemplateColumns: "1fr 2fr 1fr",
                gridTemplateRows: "1fr 2fr 1fr"
                }}>
                <div style={{
                    display: "grid",
                    gridTemplateColumns: "1fr 1fr 1fr 1fr",
                    gridTemplateRows: "auto 100px 50px 50px 50px auto",
                    gridColumn: "2",
                    gridRow: "2",
                    padding: "10px 15px",
                    borderRadius: "5px",
                    boxShadow: `0 1.2px 6px ${colors.shadow}`
                }}>
                    <h1 style={{textAlign: "center", gridArea: "2/2/2/4"}}>dMLW</h1>
                    <span style={{gridArea: "3/2/3/3"}}>Email:</span>
                    <div style={{gridArea: "3/3/3/4"}}>
                    <input type="text" name="email" style={{width:"300px"}} />
                    </div>
                    
                    <span style={{gridArea: "4/2/4/3"}}>Passwort:</span>
                    <div style={{gridArea: "4/3/4/4"}}>
                    <input type="password" name="password" style={{width:"300px"}} />
                    </div>
                    <div style={{gridArea: "5/3/5/4"}}>
                    <input type="submit" value="anmelden" onClick={() => {this.login()}} />
                    </div>
                    
                </div>
                
            </div>);
        }
    }
    async login(){
        const email = document.querySelector("input[name=email]").value;
        const password = document.querySelector("input[name=password]").value;
        const tbls = ["zettel"];
        if(email!=""&&password!=""&&await arachne.open(email, password, tbls)){
            this.setState({login: true});
        } else {
            alert("Geben Sie eine gültige Email-Adresse und Passwort ein!");
        }
    }
}

/* ************************************************** */
ReactDOM.render(
    <App />,
    document.getElementById("root")
);