export { arachne };

class ArachneTable {
    constructor(tbl, url, key){
        this.res = tbl;
        this.url = url;
        this.key = key;
    }
    async search(query, options={}){
        // options = {count:true|false, limit:100, offset:100}
        let url = `/data/${this.res}?query=${JSON.stringify(query)}`;
        if(options.count===true){url += "&count=1"}
        if(options.limit){url += "&limit="+options.limit}
        if(options.offset){url += "&offset="+options.offset}
        return await fetch(url, {headers: {"Authorization": `Bearer ${this.key}`}})
        .then(re => {
            if(re.status === 200){return re.json()}
            else{window.open("/", "_self")}
        });
    }
}

class Arachne {
    constructor(url=""){
        this.url = url;
        this.key = null;
    }
    async open(user, password, tbls=null){
        // opens a connection to the server and creates a session key
        let re = await fetch(this.url+"/session", {
            "method": "post",
            headers: {
                "Content-Type": "application/json"
            },
            "body": JSON.stringify({user: user, password: password})
        });
        if(re.status === 200){
            this.key = await re.text();

            let reUser = await fetch(this.url+"/session",
             {headers: {
                 "Authorization": `Bearer ${this.key}`
                }});
            if(re.status === 200){this.user = await reUser.json();}
            else {return false;}
            if(tbls == null){tbls = await this.tables()}
            for(const tbl of tbls){this[tbl] = new ArachneTable(tbl, this.url, this.key)}
            return true;
        }else{
            return false;
        }
    }
    async close(){
        this.key = null;
    }
}

let arachne = new Arachne();